import { Platform, Alert } from 'react-native';
import permissionManager from './permissions';

// VisionCamera Áõ∏ÂÖ≥Á±ªÂûãÂíåÂäüËÉΩ
export interface CameraConfig {
  quality: 'low' | 'medium' | 'high' | '4k';
  enableAudio: boolean;
  flashMode: 'off' | 'on' | 'auto';
  cameraPosition: 'front' | 'back';
}

export interface PhotoResult {
  path: string;
  width: number;
  height: number;
  size: number;
  timestamp: number;
}

export interface VideoResult {
  path: string;
  duration: number;
  size: number;
  timestamp: number;
}

// Voice Áõ∏ÂÖ≥Á±ªÂûãÂíåÂäüËÉΩ
export interface VoiceConfig {
  locale: string;
  continuous: boolean;
  interimResults: boolean;
  maxAlternatives: number;
  timeout: number;
}

export interface VoiceResult {
  transcript: string;
  confidence: number;
  isFinal: boolean;
  alternatives: Array<{
    transcript: string;
    confidence: number;
  }>;
}

// ‰ΩçÁΩÆÊúçÂä°Áõ∏ÂÖ≥Á±ªÂûã
export interface LocationConfig {
  accuracy: 'low' | 'balanced' | 'high' | 'highest';
  timeout: number;
  maximumAge: number;
  enableHighAccuracy: boolean;
}

export interface LocationResult {
  latitude: number;
  longitude: number;
  altitude?: number;
  accuracy: number;
  speed?: number;
  heading?: number;
  timestamp: number;
}

class NativeModulesManager {
  private cameraModule: any = null;
  private voiceModule: any = null;
  private locationModule: any = null;

  constructor() {
    this.initializeModules();
  }

  /**
   * ÂàùÂßãÂåñÂéüÁîüÊ®°Âùó
   */
  private async initializeModules() {
    try {
      // Âä®ÊÄÅÂØºÂÖ•VisionCamera
      if (Platform.OS === 'ios' || Platform.OS === 'android') {
        try {
          const { Camera } = await import('react-native-vision-camera');
          this.cameraModule = Camera;
          console.log('‚úÖ VisionCamera Ê®°ÂùóÂä†ËΩΩÊàêÂäü');
        } catch (error) {
          console.warn('‚ö†Ô∏è VisionCamera Ê®°ÂùóÊú™ÂÆâË£ÖÊàñÂä†ËΩΩÂ§±Ë¥•:', error);
        }

        // Âä®ÊÄÅÂØºÂÖ•Voice
        try {
          const Voice = await import('@react-native-voice/voice');
          this.voiceModule = Voice.default;
          console.log('‚úÖ Voice Ê®°ÂùóÂä†ËΩΩÊàêÂäü');
        } catch (error) {
          console.warn('‚ö†Ô∏è Voice Ê®°ÂùóÊú™ÂÆâË£ÖÊàñÂä†ËΩΩÂ§±Ë¥•:', error);
        }

        // Âä®ÊÄÅÂØºÂÖ•Geolocation
        try {
          const Geolocation = await import('@react-native-community/geolocation');
          this.locationModule = Geolocation.default;
          console.log('‚úÖ Geolocation Ê®°ÂùóÂä†ËΩΩÊàêÂäü');
        } catch (error) {
          console.warn('‚ö†Ô∏è Geolocation Ê®°ÂùóÊú™ÂÆâË£ÖÊàñÂä†ËΩΩÂ§±Ë¥•:', error);
        }
      }
    } catch (error) {
      console.error('‚ùå ÂéüÁîüÊ®°ÂùóÂàùÂßãÂåñÂ§±Ë¥•:', error);
    }
  }

  /**
   * Ê£ÄÊü•Áõ∏Êú∫ÂèØÁî®ÊÄß
   */
  async isCameraAvailable(): Promise<boolean> {
    if (!this.cameraModule) {
      return false;
    }

    try {
      const permission = await permissionManager.checkPermission('camera');
      if (!permission.granted) {
        return false;
      }

      // Ê£ÄÊü•ËÆæÂ§áÊòØÂê¶ÊúâÁõ∏Êú∫
      const devices = await this.cameraModule.getAvailableCameraDevices();
      return devices.length > 0;
    } catch (error) {
      console.error('Ê£ÄÊü•Áõ∏Êú∫ÂèØÁî®ÊÄßÂ§±Ë¥•:', error);
      return false;
    }
  }

  /**
   * ÊãçÁÖßÂäüËÉΩ
   */
  async takePhoto(config: Partial<CameraConfig> = {}): Promise<PhotoResult | null> {
    if (!this.cameraModule) {
      Alert.alert('ÈîôËØØ', 'Áõ∏Êú∫Ê®°ÂùóÊú™ÂàùÂßãÂåñ');
      return null;
    }

    try {
      // Ê£ÄÊü•Âπ∂ËØ∑Ê±ÇÁõ∏Êú∫ÊùÉÈôê
      const permission = await permissionManager.requestPermissionWithDialog('camera');
      if (!permission.granted) {
        return null;
      }

      const defaultConfig: CameraConfig = {
        quality: 'high',
        enableAudio: false,
        flashMode: 'auto',
        cameraPosition: 'back',
        ...config,
      };

      // Ê®°ÊãüÊãçÁÖßÁªìÊûúÔºàÂÆûÈôÖÂÆûÁé∞ÈúÄË¶ÅÈõÜÊàêVisionCameraÔºâ
      const result: PhotoResult = {
        path: `/tmp/photo_${Date.now()}.jpg`,
        width: 1920,
        height: 1080,
        size: 2048000,
        timestamp: Date.now(),
      };

      console.log('üì∏ ÊãçÁÖßÊàêÂäü:', result);
      return result;
    } catch (error) {
      console.error('ÊãçÁÖßÂ§±Ë¥•:', error);
      Alert.alert('ÊãçÁÖßÂ§±Ë¥•', 'ËØ∑Á®çÂêéÈáçËØï');
      return null;
    }
  }

  /**
   * ÂΩïÂà∂ËßÜÈ¢ëÂäüËÉΩ
   */
  async recordVideo(config: Partial<CameraConfig> = {}): Promise<VideoResult | null> {
    if (!this.cameraModule) {
      Alert.alert('ÈîôËØØ', 'Áõ∏Êú∫Ê®°ÂùóÊú™ÂàùÂßãÂåñ');
      return null;
    }

    try {
      // Ê£ÄÊü•Âπ∂ËØ∑Ê±ÇÁõ∏Êú∫ÊùÉÈôê
      const permission = await permissionManager.requestPermissionWithDialog('camera');
      if (!permission.granted) {
        return null;
      }

      const defaultConfig: CameraConfig = {
        quality: 'high',
        enableAudio: true,
        flashMode: 'off',
        cameraPosition: 'back',
        ...config,
      };

      // Ê®°ÊãüÂΩïÂà∂ÁªìÊûúÔºàÂÆûÈôÖÂÆûÁé∞ÈúÄË¶ÅÈõÜÊàêVisionCameraÔºâ
      const result: VideoResult = {
        path: `/tmp/video_${Date.now()}.mp4`,
        duration: 30000,
        size: 10240000,
        timestamp: Date.now(),
      };

      console.log('üé• ÂΩïÂà∂ÊàêÂäü:', result);
      return result;
    } catch (error) {
      console.error('ÂΩïÂà∂Â§±Ë¥•:', error);
      Alert.alert('ÂΩïÂà∂Â§±Ë¥•', 'ËØ∑Á®çÂêéÈáçËØï');
      return null;
    }
  }

  /**
   * ËØ≠Èü≥ËØÜÂà´ÂäüËÉΩ
   */
  async startVoiceRecognition(config: Partial<VoiceConfig> = {}): Promise<void> {
    if (!this.voiceModule) {
      Alert.alert('ÈîôËØØ', 'ËØ≠Èü≥Ê®°ÂùóÊú™ÂàùÂßãÂåñ');
      return;
    }

    try {
      // Ê£ÄÊü•Âπ∂ËØ∑Ê±ÇÈ∫¶ÂÖãÈ£éÊùÉÈôê
      const permission = await permissionManager.requestPermissionWithDialog('microphone');
      if (!permission.granted) {
        return;
      }

      const defaultConfig: VoiceConfig = {
        locale: 'zh-CN',
        continuous: true,
        interimResults: true,
        maxAlternatives: 3,
        timeout: 30000,
        ...config,
      };

      // ËÆæÁΩÆËØ≠Èü≥ËØÜÂà´‰∫ã‰ª∂ÁõëÂê¨Âô®
      this.voiceModule.onSpeechStart = () => {
        console.log('üé§ ÂºÄÂßãËØ≠Èü≥ËØÜÂà´');
      };

      this.voiceModule.onSpeechEnd = () => {
        console.log('üé§ ËØ≠Èü≥ËØÜÂà´ÁªìÊùü');
      };

      this.voiceModule.onSpeechResults = (event: any) => {
        const results = event.value;
        console.log('üé§ ËØ≠Èü≥ËØÜÂà´ÁªìÊûú:', results);
      };

      this.voiceModule.onSpeechError = (event: any) => {
        console.error('üé§ ËØ≠Èü≥ËØÜÂà´ÈîôËØØ:', event.error);
      };

      // ÂºÄÂßãËØ≠Èü≥ËØÜÂà´
      await this.voiceModule.start(defaultConfig.locale);
      console.log('üé§ ËØ≠Èü≥ËØÜÂà´Â∑≤ÂêØÂä®');
    } catch (error) {
      console.error('ÂêØÂä®ËØ≠Èü≥ËØÜÂà´Â§±Ë¥•:', error);
      Alert.alert('ËØ≠Èü≥ËØÜÂà´Â§±Ë¥•', 'ËØ∑Ê£ÄÊü•È∫¶ÂÖãÈ£éÊùÉÈôê');
    }
  }

  /**
   * ÂÅúÊ≠¢ËØ≠Èü≥ËØÜÂà´
   */
  async stopVoiceRecognition(): Promise<void> {
    if (!this.voiceModule) {
      return;
    }

    try {
      await this.voiceModule.stop();
      console.log('üé§ ËØ≠Èü≥ËØÜÂà´Â∑≤ÂÅúÊ≠¢');
    } catch (error) {
      console.error('ÂÅúÊ≠¢ËØ≠Èü≥ËØÜÂà´Â§±Ë¥•:', error);
    }
  }

  /**
   * Ëé∑ÂèñÂΩìÂâç‰ΩçÁΩÆ
   */
  async getCurrentLocation(config: Partial<LocationConfig> = {}): Promise<LocationResult | null> {
    if (!this.locationModule) {
      Alert.alert('ÈîôËØØ', '‰ΩçÁΩÆÊúçÂä°Ê®°ÂùóÊú™ÂàùÂßãÂåñ');
      return null;
    }

    try {
      // Ê£ÄÊü•Âπ∂ËØ∑Ê±Ç‰ΩçÁΩÆÊùÉÈôê
      const permission = await permissionManager.requestPermissionWithDialog('location');
      if (!permission.granted) {
        return null;
      }

      const defaultConfig: LocationConfig = {
        accuracy: 'high',
        timeout: 15000,
        maximumAge: 10000,
        enableHighAccuracy: true,
        ...config,
      };

      return new Promise((resolve, reject) => {
        this.locationModule.getCurrentPosition(
          (position: any) => {
            const result: LocationResult = {
              latitude: position.coords.latitude,
              longitude: position.coords.longitude,
              altitude: position.coords.altitude,
              accuracy: position.coords.accuracy,
              speed: position.coords.speed,
              heading: position.coords.heading,
              timestamp: position.timestamp,
            };
            console.log('üìç Ëé∑Âèñ‰ΩçÁΩÆÊàêÂäü:', result);
            resolve(result);
          },
          (error: any) => {
            console.error('Ëé∑Âèñ‰ΩçÁΩÆÂ§±Ë¥•:', error);
            Alert.alert('ÂÆö‰ΩçÂ§±Ë¥•', 'ËØ∑Ê£ÄÊü•‰ΩçÁΩÆÊùÉÈôêÂíåGPSËÆæÁΩÆ');
            resolve(null);
          },
          {
            enableHighAccuracy: defaultConfig.enableHighAccuracy,
            timeout: defaultConfig.timeout,
            maximumAge: defaultConfig.maximumAge,
          }
        );
      });
    } catch (error) {
      console.error('Ëé∑Âèñ‰ΩçÁΩÆÂ§±Ë¥•:', error);
      return null;
    }
  }

  /**
   * ÁõëÂê¨‰ΩçÁΩÆÂèòÂåñ
   */
  async watchLocation(
    callback: (location: LocationResult) => void,
    config: Partial<LocationConfig> = {}
  ): Promise<number | null> {
    if (!this.locationModule) {
      Alert.alert('ÈîôËØØ', '‰ΩçÁΩÆÊúçÂä°Ê®°ÂùóÊú™ÂàùÂßãÂåñ');
      return null;
    }

    try {
      // Ê£ÄÊü•Âπ∂ËØ∑Ê±Ç‰ΩçÁΩÆÊùÉÈôê
      const permission = await permissionManager.requestPermissionWithDialog('location');
      if (!permission.granted) {
        return null;
      }

      const defaultConfig: LocationConfig = {
        accuracy: 'balanced',
        timeout: 30000,
        maximumAge: 5000,
        enableHighAccuracy: false,
        ...config,
      };

      const watchId = this.locationModule.watchPosition(
        (position: any) => {
          const result: LocationResult = {
            latitude: position.coords.latitude,
            longitude: position.coords.longitude,
            altitude: position.coords.altitude,
            accuracy: position.coords.accuracy,
            speed: position.coords.speed,
            heading: position.coords.heading,
            timestamp: position.timestamp,
          };
          callback(result);
        },
        (error: any) => {
          console.error('‰ΩçÁΩÆÁõëÂê¨ÈîôËØØ:', error);
        },
        {
          enableHighAccuracy: defaultConfig.enableHighAccuracy,
          timeout: defaultConfig.timeout,
          maximumAge: defaultConfig.maximumAge,
        }
      );

      console.log('üìç ÂºÄÂßãÁõëÂê¨‰ΩçÁΩÆÂèòÂåñ, watchId:', watchId);
      return watchId;
    } catch (error) {
      console.error('ÁõëÂê¨‰ΩçÁΩÆÂ§±Ë¥•:', error);
      return null;
    }
  }

  /**
   * ÂÅúÊ≠¢ÁõëÂê¨‰ΩçÁΩÆÂèòÂåñ
   */
  clearLocationWatch(watchId: number): void {
    if (this.locationModule && watchId) {
      this.locationModule.clearWatch(watchId);
      console.log('üìç ÂÅúÊ≠¢ÁõëÂê¨‰ΩçÁΩÆÂèòÂåñ, watchId:', watchId);
    }
  }

  /**
   * Ê£ÄÊü•ÊâÄÊúâÂéüÁîüÊ®°ÂùóÁä∂ÊÄÅ
   */
  getModulesStatus(): {
    camera: boolean;
    voice: boolean;
    location: boolean;
  } {
    return {
      camera: !!this.cameraModule,
      voice: !!this.voiceModule,
      location: !!this.locationModule,
    };
  }

  /**
   * ÂÅ•Â∫∑Â∫îÁî®‰∏ìÁî®ÔºöÂêØÂä®ÂõõËØäÁõ∏ÂÖ≥ÁöÑÂéüÁîüÂäüËÉΩ
   */
  async initializeHealthFeatures(): Promise<{
    camera: boolean;
    voice: boolean;
    location: boolean;
    permissions: any;
  }> {
    console.log('üè• ÂàùÂßãÂåñÂÅ•Â∫∑Â∫îÁî®ÂéüÁîüÂäüËÉΩ...');

    // Ê£ÄÊü•Ê®°ÂùóÁä∂ÊÄÅ
    const modules = this.getModulesStatus();
    
    // Ê£ÄÊü•ÊùÉÈôêÁä∂ÊÄÅ
    const permissions = await permissionManager.checkHealthAppPermissions();

    console.log('üìä ÂéüÁîüÊ®°ÂùóÁä∂ÊÄÅ:', modules);
    console.log('üîê ÊùÉÈôêÁä∂ÊÄÅ:', permissions);

    return {
      ...modules,
      permissions,
    };
  }

  /**
   * ÂÅ•Â∫∑Â∫îÁî®‰∏ìÁî®ÔºöËØ∑Ê±ÇÊâÄÊúâÂøÖË¶ÅÊùÉÈôê
   */
  async requestHealthPermissions(): Promise<boolean> {
    console.log('üîê ËØ∑Ê±ÇÂÅ•Â∫∑Â∫îÁî®ÊùÉÈôê...');

    try {
      const results = await permissionManager.requestHealthAppPermissions();
      
      const allGranted = Object.values(results).every(result => result.granted);
      
      if (allGranted) {
        console.log('‚úÖ ÊâÄÊúâÊùÉÈôêÂ∑≤ÊéàÊùÉ');
        Alert.alert('ÊùÉÈôêÊéàÊùÉÊàêÂäü', 'ÊâÄÊúâÂäüËÉΩÁé∞Âú®ÂèØ‰ª•Ê≠£Â∏∏‰ΩøÁî®‰∫Ü');
      } else {
        console.log('‚ö†Ô∏è ÈÉ®ÂàÜÊùÉÈôêÊú™ÊéàÊùÉ:', results);
        Alert.alert(
          'ÊùÉÈôêÊéàÊùÉ‰∏çÂÆåÊï¥',
          'ÈÉ®ÂàÜÂäüËÉΩÂèØËÉΩÊó†Ê≥ïÊ≠£Â∏∏‰ΩøÁî®ÔºåÊÇ®ÂèØ‰ª•Á®çÂêéÂú®ËÆæÁΩÆ‰∏≠ÂºÄÂêØÁõ∏ÂÖ≥ÊùÉÈôê'
        );
      }

      return allGranted;
    } catch (error) {
      console.error('‚ùå ËØ∑Ê±ÇÊùÉÈôêÂ§±Ë¥•:', error);
      Alert.alert('ÊùÉÈôêËØ∑Ê±ÇÂ§±Ë¥•', 'ËØ∑Á®çÂêéÈáçËØï');
      return false;
    }
  }
}

// ÂØºÂá∫Âçï‰æãÂÆû‰æã
export const nativeModulesManager = new NativeModulesManager();
export default nativeModulesManager; 